@import url(https://use.typekit.net/bjz4eqa.css);
@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,400&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

.red-font {
  color: #ee7175;
}

body {
  font-family: "Source Sans Pro", sans-serif;
  font-style: normal;
  font-weight: 400;
  background: #626568;
  height: 100vh;
}

.column-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  grid-gap: 0.5rem;
  align-items: grid-start;
  margin-top: 0.5rem;
}

.columns {
  font-weight: 300;
  margin-top: 2vh;
}

.layout-container {
  margin-left: 4vw;
  margin-right: 4vw;
}

a {
  text-decoration: none;
}

a:visited {
  text-decoration: none !important;
  color: #ee7175;
}

.skills-title {
  font-size: 28px;
  border-bottom: solid 1px #646464;
  text-align: center;
}

.home-icon {
  font-size: 24px;
  padding-top: 1vh;
}

.from-img {
  height: 100px;
  width: 100px;
}

.info-box {
  width: 150px;
  height: 150px;
  background: transparent;
  border-radius: 360px;
  border: 6px solid #ee7175;
  margin-top: 2vw;
  margin-left: 2vw;
  margin: auto;
  box-shadow: inset 0 0 10px;
  /* border-top: 10px solid lightgreen;
  border-right: 10px solid mediumpurple;
  border-bottom: 10px solid lightsalmon;
  border-left: 10px solid moccasin; */
}

.info-box:hover {
  background: #ee7175;
}

.info-icons {
  text-align: right;
  padding-top: 1vh;
  font-size: 21px;
  font-weight: 300px;
  border-right: 2px solid #313335;
  border-bottom: 2px solid #313335;
  padding-right: 1vw;
  padding-bottom: 1vw;
  border-radius: 5px;
}

.float-left {
  float: left;
}

.float-right {
  float: right;
}

@-webkit-keyframes slideInFromLeft {
  0% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes slideInFromLeft {
  0% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@-webkit-keyframes slideInFromRight {
  0% {
    -webkit-transform: translateX(200%);
            transform: translateX(200%);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes slideInFromRight {
  0% {
    -webkit-transform: translateX(200%);
            transform: translateX(200%);
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

.slide-left {
  -webkit-animation: 1.5s ease-out 0s 1 slideInFromLeft;
          animation: 1.5s ease-out 0s 1 slideInFromLeft;
}

.slide-right {
  -webkit-animation: 1.5s ease-out 0s 1 slideInFromRight;
          animation: 1.5s ease-out 0s 1 slideInFromRight;
}

.small-circle {
  width: 50px;
  height: 50px;
  background: transparent;
  border-radius: 360px;
  border: 6px solid #ee7175;
  margin-top: 2vw;
  margin-left: 2vw;
  margin: auto;
  box-shadow: inset 0 0 10px;
}

.small-circle:hover {
  background: #ee7175;
}

.indent-box {
  box-shadow: inset 0 0 10px;
  border-radius: 5px;
}

