  box-shadow: inset 0 0 10px;
  border-radius: 5px;
}

div.column-list:nth-child(1) > div:nth-child(1) {
  box-shadow: inset 0 0 10px;
  border-radius: 5px;
} */

.skills-text {
  border-left: 2px solid #313335;
  border-bottom: 2px solid #313335;
  padding-left: 1vw;
  padding-bottom: 1vw;
  border-radius: 5px;
  text-align: justify;
  padding-top: 1vh;
  font-size: 21px;
}

.circle-image {
  width: 260px;
  height: 260px;
  border-radius: 50%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 1vh;
  background-image: url(/static/media/me.e1895821.png);
  background-position: center;
  background-size: cover;
  margin-bottom: 1vh;
  border: 2px solid #ee7175;
}

.banner-background {
  margin: 0;
  padding-top: 2vh;
  padding-bottom: 2vh;
  color: white;
  text-align: center;
}

.nav-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  border-bottom: #ee7175 1px solid;
}

.navs {
  color: #ee7175;
}

.my-name {
  text-align: center;
  font-size: 36px;
  padding-top: 1.5vh;
}

div.navs:nth-child(3) {
  font-size: 24px;
  text-align: end;
}

.contact-me {
  padding-right: 2vw;
  padding-top: 1.5vh;
}

.logo-me {
  padding-left: 2vw;
  height: 36px;
  padding-top: 1vh;
}

.app-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  text-align: center;
}

.apps {
  color: #ee7175;
  font-size: 30px;
  padding-top: 1vh;
  padding-bottom: 1vh;
  border-bottom: #ee7175 1px solid;
}

.center-app {
  border-left: #ee7175 solid 1px;
  border-right: #ee7175 solid 1px;
}

.apps:hover {
  background: #ee7175;
  color: #626568;
}

.footer {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  background: #ee7175;
  overflow: hidden;
  position: fixed;
  bottom: 0;
  width: 100vw;
}

.footer-options {
  color: #626568;
  padding-top: 0.5vh;
  padding-bottom: 0.5vh;
}

.social-icon {
  font-size: 28px;
  padding-left: 1vw;
}

a.social-icon:visited {
  color: #626568;
}

a.social-icon {
  color: #626568;
}

